import classnames from "classnames";
import * as React from "react";

const SectionDescription = ({
  centered = false,
  mini = false,
  small = false,
  clear = false,
  className,
  children,
} = {}) => (
  <div
    className={classnames("section__description", className, {
      "section__description--mini": mini,
      "section__description--centered": centered,
      "section__description--small": small,
      "section__description--clear": clear,
    })}
  >
    {children}
  </div>
);

export default SectionDescription;
