import React from "react";
import { graphql } from "gatsby";

import ReadyToStart from "components/ReadyToStart";
import SectionTitle from "components/SectionTitle";
import PressPostPreview from "components/PressPostPreview";
import Layout from "components/layout";

const Press = ({
  data: {
    allMarkdownRemark: { edges: posts },
  },
  location,
}) => (
  <Layout
    location={location}
    title="News and Press | Brex"
    description="Want the latest on Brex? Discover all of our current company news, big announcements and press coverage right here!"
  >
    <section className="section" id="posts">
      <div className="section__content">
        <SectionTitle className="text-center mb-5">News and Press</SectionTitle>
        <div className="row">
          {posts.map(({ node: post }) => (
            <PressPostPreview post={post} key={post.id} />
          ))}
        </div>
      </div>
    </section>
    <ReadyToStart />
  </Layout>
);

export default Press;

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date, frontmatter___title] }
    ) {
      edges {
        node {
          excerpt(pruneLength: 250)
          id
          frontmatter {
            date
            path
            title
          }
        }
      }
    }
  }
`;
