import React from "react";

import InternalLink from "components/reusable/atoms/links/InternalLink";
import OutboundLink from "components/reusable/atoms/links/OutboundLink";
import { formatDateTime } from "utils/formatting";

const PressPostPreview = ({ post }) => (
  <div className="press-post">
    <div className="press-post__content">
      <p className="press-post__date">
        {formatDateTime(post.frontmatter.date)}
      </p>
      {post.frontmatter.path.startsWith("/") ? (
        <InternalLink className="press-post__title" to={post.frontmatter.path}>
          {post.frontmatter.title}
        </InternalLink>
      ) : (
        <OutboundLink
          className="press-post__title"
          href={post.frontmatter.path}
          target="_blank"
        >
          {post.frontmatter.title}
        </OutboundLink>
      )}

      <p className="press-post__preview">{post.excerpt}</p>
    </div>
  </div>
);

export default PressPostPreview;
