import classnames from "classnames";
import * as React from "react";

const SectionTitle = ({
  small = false,
  large = false,
  subtitle = false,
  highlight = false,
  darker = false,
  headline = false,
  heavy = false,
  children,
  className,
}) => (
  <h1
    className={classnames("section__title", className, {
      "section__title--small": small,
      "section__title--large": large,
      "section__title--subtitle": subtitle,
      "section__title--highlight": highlight,
      "section__title--darker": darker,
      "section__title--headline": headline,
      "section__title--heavy": heavy,
    })}
  >
    {children}
  </h1>
);

export default SectionTitle;
